@import "https://fonts.googleapis.com/css2?family=Lato&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Lato, sans-serif;
}

body {
  margin: 0;
}

h1 {
  font-size: 60px;
}

h2 {
  font-size: 36px;
}

p {
  font-size: 18px;
}

section {
  padding: 0;
  overflow: hidden;
}

.slider {
  width: 100%;
  max-height: 540px;
  object-fit: cover;
}

.nav {
  height: 70px;
  width: 100%;
  z-index: 1;
  transition: 0;
  background-color: #0e294b;
  border-radius: 2px;
  place-content: center space-between;
  align-items: center;
  margin: 35px 0 0;
  padding-left: 35px;
  padding-right: 10px;
  display: flex;
  position: fixed;
  box-shadow: 0 12px 9px #0006;
}

.nav .nav-item a {
  color: #fff;
  transition: .2;
  border: 1px solid #ffffff80;
  border-radius: 2px;
  padding: 10px 30px;
  text-decoration: none;
  display: flex;
  box-shadow: 3px 3px 3px #00000040;
}

.nav .nav-item:hover {
  opacity: 1;
  transition: all .4s;
  transition: .2;
  background-color: #4682b4;
  border-radius: 1px;
  box-shadow: 2px 2px 2px #00000040;
}

.nav .nav-links {
  align-items: center;
  padding-right: 50px;
  list-style: none;
  display: flex;
}

.nav .nav-links .nav-links a:nth-of-type(1) {
  justify-content: space-between;
  display: flex;
}

.nav .nav-links .nav-item {
  transition: all .4s;
}

.nav .nav-links #register {
  color: #e9e9e9;
  opacity: .8;
  transition: all .4s;
  transition: .2;
  background: #5ac348;
  border-radius: 1px;
  margin: 0 25px;
  text-decoration: none;
  display: inline-block;
  box-shadow: 2px 2px 2px #00000040;
}

.nav .nav-links #register:hover {
  color: #222;
  opacity: 1;
  transition: all .4s;
  transition: .2;
  background-color: #1dd200;
  border-radius: 1px;
  box-shadow: 2px 2px 2px #00000040;
}

.nav .nav-links .carrito {
  opacity: .7;
  width: 40px;
  transition: all .4s;
}

.nav .nav-links .carrito:hover {
  opacity: 1;
  width: 40px;
  transition: all .4s;
}

.nav .nav-links .search {
  padding-right: 250px;
}

.nav .nav-links .input {
  background-color: #4682b4;
  border-width: 2px;
  border-radius: 2px;
  padding: 10px 20px 10px 10px;
  position: relative;
  box-shadow: inset 6px 6px 12px #0006;
}

.responsive {
  width: 100%;
  height: auto;
}

.logo img {
  width: 350px;
}

.logo img:hover {
  width: 450px;
}

.logo img, .logo img:hover {
  vertical-align: middle;
  justify-content: center;
  padding-bottom: 10px;
  transition: all .4s;
}

.boxes {
  justify-content: center;
  align-items: center;
  display: flex;
}

.boxes .blueBox {
  height: 342px;
  width: 342px;
  max-width: 342px;
  min-width: 342px;
  color: #fff;
  text-align: center;
  background-color: #0e294b;
  margin: 20px;
}

